<template>
  <el-container>
    <el-header style="height:35px">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/apiAutoTest/projectList' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-header>
    <el-main>
      <div class="searchProject">
        <el-form ref="searchForm" :model="finalSearchParams" :rules="rules" label-width="75px" style="display:flex; justify-content: left;">
          <el-form-item prop="selectedParamsValue" class="choose_params">
            <el-input placeholder="" @change="onSelectInputChange" v-model="finalSearchParams.selectedParamsValue" clearable  class="input-with-select">
              <el-select @change="getChangedParam" v-model="defaultSelectedParamsLabel" slot="prepend" placeholder="">
                <el-option v-for="item in searchParams.paramsSelection" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          <el-form-item label="" prop="creator" style="margin-left:30px">
            <el-input class="select_projectCreate" v-model="finalSearchParams.creator" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="status" style="margin-left:30px">
            <el-select class="select_projectStatus" @change="getQueryStatus" v-model="defaultSelectedStatus" clearable placeholder="">
              <el-option
                v-for="status in searchParams.statusSelection"
                :key="status.value"
                :label="status.label"
                :value="status.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button class="searchBtn" type="primary" @click="queryProject('searchForm')"></el-button>
            <el-button class="addProjectBtn" size="mini" @click="onCreateProject"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="projectTableList">
        <el-table
          :data="tableData"
          style="width: 100%">
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.projectName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.projectType }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.dept }}</span>
            </template>
          </el-table-column>
          <el-table-column label="IT" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.productLine }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            width="160">
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.projectCycle }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.status }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.creator }}</span>
            </template>
          </el-table-column>
          <el-table-column label="" width="160">
            <template slot-scope="scope">
              <span>{{ scope.row.update_time }}</span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="projectEdit(scope.$index, scope.row)">
              </el-button>
              <el-button
                size="mini"
                @click="projectDelete(scope.row)">
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-main>
  </el-container>
</template>
<script>
import ConfirmMixin from '../../assets/confirm_mixin'
import createProject from './createProject.vue'
import editProject from './editProject.vue'
export default {
  mixins: [ConfirmMixin],
  data () {
    return {
      searchParams: { // 
        paramsSelection: [{
          value: 'projectName',
          label: ''
        },
        {
          value: 'projectType',
          label: ''
        },
        {
          value: 'productLine',
          label: 'IT'
        },
        {
          value: 'code',
          label: ''
        }],
        statusSelection: [
          {
            value: '1',
            label: ''
          },
          {
            value: '0',
            label: ''
          }]
      },
      defaultSelectedParamsLabel: '',
      defaultSelectedStatus: '',
      finalSearchParams: {
        selectedParamsKey: '',
        selectedParamsValue: '',
        creator: '',
        status: ''
      },
      rules: {
        selectedParamsKey: [{require: true, message: '', trigger: 'blur'}],
        selectedParamsValue: [{require: false, trigger: 'blur'}],
        creator: [{require: false, trigger: 'blur'}],
        status: [{require: false, trigger: 'blur'}]
      },
      tableData: [],
      addProjectParams: {},
      editProjectParams: {}
    }
  },
  methods: {
    onCreateProject () { // 
      this.confirmBox({
        title: '',
        customClass: 'createProject_confirmBox',
        closeOnClickModal: false, //  MessageBox
        showCancelButton: true,
        showConfirmButton: true,
        confirmButtonText: '',
        component: createProject,
        componentName: 'createProject',
        confirmValidate: (action, formParms, done) => {
          if (action === 'cancel') {
            formParms.clearValidate() // 
            return done() // done MessageBox 
          }
          formParms.validate(valid => {
            if (!valid) return
            this.addProjectParams = {...formParms.addRuleForm}
            let projectStartTime = new Date(this.addProjectParams.projectCycle[0])
            let projectEndTime = new Date(this.addProjectParams.projectCycle[1])
            if (projectStartTime.getTime() >= new Date().getTime()) { // 
              this.addProjectParams['status'] = '1' // 
            } else {
              this.addProjectParams['status'] = '2' // 
            }
            if (projectEndTime.getTime() < new Date().getTime()) {
              this.addProjectParams['status'] = '0' // 
            } else {
              this.addProjectParams['status'] = '1' // 
            }
            formParms.clearValidate() // 
            done()
            this.addProjectParams['creator'] = 'hemeilong' // 
            let param = JSON.stringify(this.addProjectParams)
            return this.$axios.post('/apiAutoTest/projectManage/projectList/addPro', param).then(response => {
              if (response.status === 200) {
                console.log('Ajax,', response.data)
                this.$message({
                  message: '',
                  type: 'success',
                  color: 'green'
                })
                this.queryProject('searchForm')
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'error'
                })
              }
            }).catch(response => {
              console.log('Ajax,' +
              '', response)
            })
          })
        }
      }).catch(() => { })
    },
    getChangedParam (selectedTtem) { // keyvaluevalue
      this.finalSearchParams['selectedParamsKey'] = selectedTtem
      this.finalSearchParams.selectedParamsValue = '' // 
    },
    onSelectInputChange (inputValue) { // value
      this.finalSearchParams['selectedParamsValue'] = inputValue
    },
    getQueryStatus (status) {
      if (typeof (status) === 'undefined') {
        this.finalSearchParams['status'] = '' // 
      } else {
        this.finalSearchParams['status'] = status
      }
    },
    queryProject (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios
            .get('/apiAutoTest/projectManage/projectList/find', { params: this.finalSearchParams })
            .then(response => {
              let responseJsonData = response.data
              this.tableData = responseJsonData['data']['data']
            })
            .catch(error => {
              console.log('' + error)
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    projectEdit (index, row) {
      let tempRow = {}
      for (const i in row) {
        if (i === 'projectCycle') {
          tempRow[i] = [row[i].slice(0, 19), row[i].slice(20)] // 
        } else {
          tempRow[i] = row[i]
        }
      }
      this.confirmBox({
        title: '',
        customClass: 'editProject_confirmBox',
        closeOnClickModal: false, //  MessageBox
        showCancelButton: true,
        showConfirmButton: true,
        confirmButtonText: '',
        component: editProject,
        componentName: 'editProject',
        confirmData: tempRow,
        confirmValidate: (action, instance, done) => {
          if (action === 'cancel') {
            return done() // done MessageBox 
          }
          instance.validate(valid => {
            if (!valid) return
            this.editProjectParams = {...instance.editRuleForm}
            let projectStartTime = new Date(this.editProjectParams.projectCycle[0])
            let projectEndTime = new Date(this.editProjectParams.projectCycle[1])
            if (projectStartTime.getTime() > new Date().getTime()) { // 
              this.editProjectParams['status'] = '2' // 
            } else if (projectStartTime.getTime() <= new Date().getTime() && projectEndTime.getTime() > new Date().getTime()) {
              this.editProjectParams['status'] = '1' // 
            }
            if (projectEndTime.getTime() < new Date().getTime()) {
              this.editProjectParams['status'] = '0' // 
            }
            done()
            console.log(new Date())
            this.editProjectParams['update_time'] = new Date().getTime()
            this.editProjectParams['creator'] = 'hemeilong' // 
            // 
            delete (this.editProjectParams.created_time)
            let param = JSON.stringify(this.editProjectParams)
            return this.$axios.post('/apiAutoTest/projectManage/projectList/editPro', param).then(response => {
              if (response.status === 200) {
                console.log('Ajax,', response.data)
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.queryProject('searchForm')
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'error',
                  color: 'green'
                })
              }
            }).catch(response => {
              console.log('Ajax,' +
                '', response)
            })
          })
        }
      }).catch(() => { })
    },
    projectDelete (row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let param = JSON.stringify({'id': row.id})
        return this.$axios.post('/apiAutoTest/projectManage/projectList/delPro', param).then(response => {
          if (response.status === 200) {
            console.log('Ajax,', response.data)
            this.$message({
              message: '',
              type: 'success'
            })
            this.queryProject('searchForm')
          } else {
            this.$message({
              showClose: true,
              message: '',
              type: 'error',
              color: 'green'
            })
          }
        }).catch(response => {
          console.log('Ajax,' +
            '', response)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  mounted () {
    this.getChangedParam('projectName')
    this.getQueryStatus('1')
    this.queryProject('searchForm')
  }
}
</script>
<style scoped>
  .el-container {
    height: 100%;
  }
  .el-header {
    margin-top: 2px;
  }
  .el-breadcrumb>>>.el-breadcrumb__inner.is-link:hover {
    color: #04aa51;;
  }
  .searchProject {
    width: 100%;
    height: 40px;
    padding-bottom: 40px;
  }
  .el-form-item.choose_params.is-success>>>.el-form-item__content {
    margin-left: 0px !important;
  }
  .input-with-select>>>.el-input-group__prepend {
    background-color: #fff;
    color: #606266;
    width: 100px;
    font-size: 14px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }
  .input-with-select.el-input-group--prepend>>>.el-input__inner {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }
  .input-with-select>>>.el-input__inner::-webkit-input-placeholder {
    color: #c3c3c5;
    font-size: 14px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    border-color: #04aa51;
  }
  .input-with-select>>>.el-input__inner:hover {
    border-color: #04aa51;
  }
  .input-with-select>>>.el-input__inner:focus {
    border-color: #04aa51;
  }
  .el-input-group__prepend div.el-select>>>.el-input__inner {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }
  .el-select.select_projectStatus>>>.el-input__inner {
    color: #606266;
    font-size: 14px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    width: 100px;
  }
  .el-select.select_projectStatus>>>.el-input__inner:focus {
    border-color: #04aa51;
  }
  .el-select.select_projectStatus>>>.el-input__inner:hover {
    border-color: #04aa51;
  }
  .select_projectCreate.el-input--suffix>>>.el-input__inner {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }
  .select_projectCreate.el-input>>>.el-input__inner:focus {
    border-color: #04aa51;
  }
  .select_projectCreate.el-input>>>.el-input__inner:hover {
    border-color: #04aa51;
  }
  .searchProject>.el-button:focus, .el-button:hover {
    color: #ffffff;
    border-color: #04aa51;
    background-color: #04aa51;
  }
  .el-button--primary {
    color: #FFF;
    background-color: #04aa51;
    border-color: #04aa51;
  }
  .projectTableList {
    margin-top: 30px;
    border-top: 1px solid #c0c4ccb8;
  }
  .projectTableList>>>.el-table th {
    background-color: #f4f5f975;
  }
  .projectTableList>>>.el-table tr {
    background-color: #f4f5f975;
  }
  .searchProject>>>.el-button.addProjectBtn.el-button--default.el-button--mini {
    padding: 14px 22px;
  }
</style>



